.input_box {
    margin: auto;
	display: inline;
	border-spacing: 10px;
    div {
        margin: auto;
        width: fit-content;
    }
    input {
        background-color: transparent;
        border: 1px solid #0BF3BC;
        border-radius: 15px;
        color: white;
        padding: 10px;
        margin: 10px;
        width: 80%;
        // box-shadow: 0px 0px 10px rgba(11, 243, 188, 0.5);
    }
}

